*, :before, :after {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul, ol, li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
}

html, body {
  height: 100%;
}

body {
  color: #333;
  font-family: Manrope;
  line-height: 1;
}

.wrapper {
  flex-direction: column;
  min-height: 100%;
  display: flex;
  overflow: hidden;
}

.page {
  flex: auto;
}

.title {
  font-size: 2.5rem;
  font-weight: 600;
  line-height: 3.4375rem;
}

.title__center {
  text-align: center;
}

@media (width <= 767.98px) {
