.item-how {
  text-align: center;
  justify-items: center;
  display: grid;
  position: relative;
}

.item-how__icon {
  border-radius: 20%;
  justify-content: center;
  align-items: center;
  width: 5.625rem;
  height: 5.625rem;
  margin-bottom: 3.125rem;
